'use client';

import { useEffect, useState } from 'react';

export default function DebugPage() {
  const [mounted, setMounted] = useState(false);
  const [apiTest, setApiTest] = useState<any>(null);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    setMounted(true);
    
    // Test direct API call
    fetch('http://localhost:8000/tasks')
      .then(res => res.json())
      .then(data => {
        console.log('‚úÖ Direct fetch success:', data);
        setApiTest(data);
      })
      .catch(err => {
        console.error('‚ùå Direct fetch error:', err);
        setError(err.message);
      });
  }, []);

  if (!mounted) {
    return <div>üîß Mounting...</div>;
  }

  return (
    <div className="p-4">
      <h1 className="text-2xl font-bold mb-4">üîß Debug Page</h1>
      
      <div className="space-y-4">
        <div className="p-4 border rounded">
          <h2 className="font-semibold">React Hydration</h2>
          <p>‚úÖ Component mounted successfully</p>
        </div>

        <div className="p-4 border rounded">
          <h2 className="font-semibold">Direct API Test</h2>
          {error ? (
            <p className="text-red-500">‚ùå Error: {error}</p>
          ) : apiTest ? (
            <div>
              <p className="text-green-500">‚úÖ API working</p>
              <p>Tasks count: {apiTest.length}</p>
            </div>
          ) : (
            <p>‚è≥ Testing API...</p>
          )}
        </div>
      </div>
    </div>
  );
}